@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(246, 246, 246);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
@font-face {
  font-family: 'ProximaNova-regular';
  src: url(/ecommerce/static/media/ProximaNova-Regular.ea03f826.otf);
  font-weight: normal;
  font-style: normal;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #282a2f;
}

Link {
  text-decoration: none;
}

li, p {
  font-family: 'ProximaNova-regular', sans-serif;
  font-weight: 400;
  font-size: 13px;
  color: #edf0f1;
  text-decoration: none;
  margin: 0;
}

p {
  margin: 5px;
}

header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 5%;
  background-color: #25242a;
  position: fixed;
  top: 0;
  width: 90%;
  z-index: 1;
}

img {
  width: 100%;
}

.nav_logo {
  cursor: pointer;
  margin-right: auto;
  width: 10%;
}

.nav_links {
  list-style: none;
  margin: 5px 0 10px;
}

.nav_links li {
  display: inline-block;
  padding: 0px 20px;
}

.nav_links li {
  transition: all 0.3s ease 0s;
}

.nav_links li:hover {
  color: #0088a9;
  transition: all 0.3s ease 0s;
  cursor: pointer;
}

.home, .details, .login, .signup {
  margin-top: 5%;
}

.loader, .signup, .prod-detail_card {
  margin-top: 5%;
  text-align: center;
  color: #edf0f1;
}

.home_title, .prod-detail_title {
  text-align: center;
  margin: 25px auto 25px;
  font-size: 20px;
}

.prod_card {
  background-color: #edf0f1;
  display: -webkit-flex;
  display: flex;
  width: 30%;
  margin: 0 auto 30px;
  padding: 10px;
  border-radius: 15pt;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0px 0px 5px 1px black;
  transition: all 0.2s ease 0s;
}

.prod_card:hover {
  box-shadow: 0px 0px 15px 2px black;
}

.prod_img {
  mix-blend-mode: multiply;
  width: 50%;
  transition: all 0.3s ease 0s;
  margin: 10px;
}

.prod_img:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.prod_text {
  width: 50%;
  color: #282a2f;
  margin: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.prod_price, .prod_name, .log_title {
  color: #282a2f;
}

.prod_price {
  font-weight: 500;
  font-size: 13pt;
}

.prod_name {
  font-size: 9pt;
}

.prod_button {
  background-color: #282a2f;
  border: none;
  color: #edf0f1;
  padding: 5px 3px;
  margin: 1px;
  font-family: 'ProximaNova-regular', sans-serif;
  width: 100%;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  font-size: 8pt;
}

.prod_button:hover {
  background-color: #0088a9;
}

/* details */
.prod-detail_card {
  background-color: #edf0f1;
  display: -webkit-flex;
  display: flex;
  width: 50%;
  margin: 0 auto 30px;
  padding: 10px;
  border-radius: 15pt;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0px 0px 5px 1px black;
  transition: all 0.2s ease 0s;
}

.prod-detail_card:hover {
  box-shadow: 0px 0px 15px 2px black;
}

.prod-detail_img {
  mix-blend-mode: multiply;
  width: 60%;
  transition: all 0.3s ease 0s;
  margin: 10px;
}

.prod-detail_img:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.prod-detail_text {
  width: 40%;
  color: #282a2f;
  margin: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.prod-detail_price, .prod-detail_name, .prod-detail_description, .prod-detail_stock, .prod-detail_sales {
  color: #282a2f;
}

.prod-detail_price {
  font-weight: 500;
  font-size: 13pt;
}

.prod-detail_name {
  font-size: 13pt;
  font-weight: 500;
  text-decoration: underline;
}

.prod-detail_description {
  font-size: 9pt;
}

.prod-detail_stock, .prod-detail_sales {
  font-size: 8pt;
  opacity: 0.5;
}

.prod-detail_category {
  text-transform: uppercase;
}

.prod-detail_button {
  background-color: #282a2f;
  border: none;
  color: #edf0f1;
  padding: 8px 3px;
  margin: 1px;
  font-family: 'ProximaNova-regular', sans-serif;
  width: 100%;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  font-size: 10pt;
}

.prod-detail_button:hover {
  background-color: #0088a9;
}

/* login */
.log_card {
  background-color: #edf0f1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 25%;
  margin: 0 auto 30px;
  padding: 30px 10px;
  border-radius: 15pt;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0px 0px 5px 1px black;
  transition: all 0.2s ease 0s;
}

.log_card:hover {
  box-shadow: 0px 0px 15px 2px black;
}

.log_form {
  width: 80%;
}

.log_title {
  font-size: 25px;
}

.log_field input {
  width: 100%;
  margin: 10px 0;
  padding: 0 5px 0;
  font-size: 10pt;
  border: none;
  border-bottom: 1px solid #282a2f;
  background: none;
  outline: none;
  font-family: 'ProximaNova-regular', sans-serif;
  color: #282a2f;
  transition: all 0.5s ease 0s;
}


.log_field input:hover {
  border-bottom: 1px solid #0088a9;
}

.log_field input:focus,
.log_field input:valid {
  border-bottom: 2px solid #0088a9;
}


::-webkit-input-placeholder {
  color: #282a2f;
  opacity: 0.5;
}


:-ms-input-placeholder {
  color: #282a2f;
  opacity: 0.5;
}


::placeholder {
  color: #282a2f;
  opacity: 0.5;
}


.log_pass {
  color: #282a2f;
  cursor: pointer;
  font-family: 'ProximaNova-regular', sans-serif;
  font-size: 10pt;
  text-align: center;
  transition: .5s
}

.log_pass:hover {
  color: #0088a9;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.log_button {
  background-color: #282a2f;
  border: none;
  color: #edf0f1;
  padding: 8px 3px;
  margin: 10px 0;
  font-family: 'ProximaNova-regular', sans-serif;
  width: 100%;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  font-size: 10pt;
}

.log_button:hover {
  background-color: #0088a9;
}

.log_button a {
  color: #edf0f1;
  text-decoration: none;
}

.log_signup {
  color: #282a2f;
  font-family: 'ProximaNova-regular', sans-serif;
  font-size: 10pt;
  text-align: center;
  text-decoration: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  list-style: none;
}

.log_signup a {
  color: #282a2f;
  transition: all 0.3s ease 0s;
}

.log_signup a:hover {
  color: #0088a9;
}

